<h2 id="dropdown-下拉列表" tabindex="-1"><a class="header-anchor" href="#dropdown-下拉列表" aria-hidden="true">#</a> Dropdown 下拉列表</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
    <div class="component-box-top"><ClientOnly>
        <wd-dropdown
            :visible="false"
            trigger="hover"
            @visibleChange="visibleChangeHandler"
        >
            <a class="wd-dropdown-link">
            下拉菜单
            <down-outlined></down-outlined>
            </a>
            <template #dropdown>
                <wd-dropdown-menu>
                    <wd-dropdown-menu-item>
                    <a href="https://www.baidu.com">百度</a>
                    </wd-dropdown-menu-item>
                    <wd-dropdown-menu-item disabled>
                    <a href="https://www.baidu.com">百度</a>
                    </wd-dropdown-menu-item>
                    <wd-dropdown-menu-item>
                    <a href="javascript:;">1st menu item</a>
                    </wd-dropdown-menu-item>
                </wd-dropdown-menu>
            </template>
        </wd-dropdown>
    </ClientOnly></div>
    <div class="component-box-bottom">
    <div class="component-title">下拉组件</div>
    <div class="component-desc">
        点击或者鼠标移到组件上可以出现下拉的选项，每个选项可以自定义样式；
    </div>
    </div>
</div>
